<template>
  <div id="housedeal" class="row">
    <!-- Header Start -->
    <!--    <div class="header">
      <h2 class="my-5">거래가 검색</h2>
    </div>-->
    <!-- Header End -->

    <!-- 좌측 검색 박스 -->
    <div id="left-bar" class="col-4">
      <form class="form-inline">
        <div class="content mb-3 shadow" style="align-items: center">
          <div class="row ml-3">
            <div>
              <nav class="navbar navbar-expand-sm bg-white navbar-dark">
                <div class="dropdown-header">
                  <select class="form-control" name="key" id="key">
                    <option value="dong">동</option>
                    <option value="AptName">아파트</option>
                  </select>
                </div>
                <div>
                  <input
                    class="form-control mr-sm-2"
                    type="text"
                    placeholder="동, 이름으로 검색"
                    name="toggleSearch"
                    id="toggleSearch"
                  />
                </div>
                <div>
                  <button class="btn" style="background-color: #f03c02" id="btnSearch">
                    검 색
                  </button>
                </div>
              </nav>
            </div>
          </div>
        </div>
      </form>

      <div class="card shadow mb-3">
        <h4 class="p-3">거래내역</h4>
        <div style="width: 100%; height: 500px; text-align: left">
          <div v-for="detail in info" v-bind:key="detail" class="card shadow">
            이름 : {{ detail.dname }}<br />
            면적 : {{ detail.darea }}<br />
            가격 : {{ detail.dvalue }}<br />
            주소 : {{ detail.djuso }}
          </div>
          <div id="paginationWrapper"></div>
        </div>
      </div>
    </div>
    <!-- 상단 검색 박스 -->

    <!-- Map Start -->
    <div class="col-8 card p-3 mb-4 shadow" id="map">
      <div class="mb-3"></div>
    </div>
  </div>
</template>

<script>
import http from "@/util/http-common";

export default {
  name: "HouseDeal",
  data() {
    return {
      info: [
        {
          dname: "건양 하늘터",
          dvalue: "84,500",
          dyear: "2010-01-01",
          darea: "27",
          ddate: "2021-01-01",
          djuso: "서울특별시 종로구 명륜1가",
          djibun: "23",
          dlng: "37.58761831486942",
          dlat: "126.99784166901551",
        },
        {
          dname: "e 편한 세상",
          dvalue: "104,500",
          dyear: "2010-01-01",
          darea: "32",
          ddate: "2021-01-01",
          djuso: "서울특별시 종로구 명륜3가",
          djibun: "23",
          dlng: "37.58761831486942",
          dlat: "126.99784166901551",
        },
        {
          dname: "자이",
          dvalue: "90,000",
          dyear: "2010-01-01",
          darea: "18",
          ddate: "2021-01-01",
          djuso: "서울특별시 강남구 강남대로",
          djibun: "23",
          dlng: "37.58761831486942",
          dlat: "126.99784166901551",
        },
      ],
    };
  },
  mounted() {
    window.kakao && window.kakao.maps ? this.initMap() : this.addKakaoMapScript();
  },
  created: {},
  methods: {
    getData() {
      http.get("", {}).then(({ data }) => {
        let idx = 0;
        if (data != null && data != "")
          this.info[idx++] = {
            dname: data.dname,
            dvalue: data.dvalue,
            dyear: data.dyear,
            darea: data.darea,
            ddate: data.ddate,
            djuso: data.djuso,
            djibun: data.djibun,
            dlng: data.dlgn,
            dlat: data.dlat,
          };
      });
    },
    addKakaoMapScript() {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=968a4feff63e4692f6229410c2a55915";
      document.head.appendChild(script);
    },
    initMap() {
      var container = document.getElementById("map"); //지도를 담을 영역의 DOM 레퍼런스
      var options = {
        //지도를 생성할 때 필요한 기본 옵션
        center: new kakao.maps.LatLng(37.58761831486942, 126.99784166901551), //지도의 중심좌표.
        level: 3, //지도의 레벨(확대, 축소 정도)
      };
      var map = new kakao.maps.Map(container, options); //지도 생성 및 객체 리턴

      var marker = new kakao.maps.Marker({ position: map.getCenter() });
      marker.setMap(map);
    },
  },
};
</script>
<style></style>